import React, { useState } from 'react';
import axios from 'axios';
import { useHistory } from 'react-router-dom';
import { ButtonGroup, ToggleButton } from 'react-bootstrap';
import '../App.css';

function AddCityState() {

const [checked, setChecked] = useState(false);
const [cityName, setCityName] = useState('');
const [stateName, setStateName] = useState('');

const history = useHistory();
const [message, setMessage] = useState('');

const addCity = async event => {
event.preventDefault();

var obj = {
name:cityName,
state:stateName,
country:'',
jwtToken:localStorage.accessToken};
var js = JSON.stringify(obj);

var config = {
method: 'post',
url: 'https://rate-my-city.herokuapp.com/api/createCity',
// url: 'https://jsonplaceholder.typicode.com/posts',
headers: { 'Content-Type': 'application/json' },
data: js
};

axios(config)
.then(function (response) {
var res = response.data;
if (res.error) {
setMessage(res.error);
} else {
setMessage('');
returnFromAddCity(res);
}
})
}

function returnFromAddCity(result) {
localStorage.setItem('accessToken', result.jwtToken.accessToken);
history.push("/");
}

function handleClickCancel() {
history.push("/");
}


return(
<div className="container-fluid h-custom mt-0">
<div className="row d-flex justify-content-center align-items-center h-100">
<div className="col-md-8 col-lg-6 col-xl-4 offset-xl-1">
<form >
<div style={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '8vh'}}>
<h1>Add City</h1>
</div>

<div className="divider d-flex align-items-center mt-0 my-4">
<p className="text-center fw-bold mx-3 mb-3"></p>
</div>
<div className="form-outline mt-1 mb-2">
<input type="text" autoFocus
className="form-control form-control-lg"
value={cityName}
onChange={(e) => {setCityName(e.target.value)}}
placeholder="Only enter US city"
/>
<label className="form-label">Enter City</label>
</div>

<div className="form-outline mb-2">
<input type="text"
className="form-control form-control-lg"
value={stateName}
onChange={(e) => {setStateName(e.target.value)}}
placeholder="Only enter US state"
/>
<label className="form-label">Enter State</label>
</div>

<div className="form-group row d-flex justify-content-center mt-4">
<div className="col-md-3">
<button type="button" className="btn btn-secondary btn-lg align-items-center" onClick={addCity}>Add</button>
</div>

<div className="col-md-3">
<button type="button" className="btn btn-dark btn-lg align-items-center" onClick={handleClickCancel}>Cancel</button>
</div>

<div className="divider d-flex align-items-center mt-1 my-4">
<p className="text-center fw-bold mx-3 mb-5">{message}</p>
</div>
</div>

</form>
</div>
</div>
</div>

);
};

export default AddCityState;